import React, { useState } from "react";
import { useParams } from "react-router-dom";
import { useDispatch } from "react-redux";
import { ADD } from "../../redux/actions/action.js";

import IMG_Mota from "../../assets/IMG_Mota.jpg";

import Box from "@mui/material/Box";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import Button from "@mui/material/Button";

import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "./details.scss";
import { data } from "../../data/data.js";

const Details = () => {
  const { id } = useParams();
  const [size, setSize] = useState("");
  const product = data.filter((item) => item.id === id);
  const handleChange = (event) => {
    setSize(event.target.value);
  };

  const dispatch = useDispatch();

  const notifySuccess = (message) =>
    toast.success(message, {
      position: "top-right",
      autoClose: 1000,
      hideProgressBar: false,
      closeOnClick: true,
      draggable: true,
      progress: undefined,
      theme: "light",
    });

  const notifyWarn = (message) =>
    toast.warn(message, {
      position: "top-right",
      autoClose: 2000,
      hideProgressBar: false,
      closeOnClick: true,
      draggable: true,
      progress: undefined,
      theme: "light",
    });

  const add = (e, size) => {
    if (size === "") {
      notifyWarn("Choose your size");
    } else {
      dispatch(ADD(e, size));
      notifySuccess("Cp nht gi hng thnh cng");
    }
  };

  return (
    <div className="detailsContainer">
      <ToastContainer />
      <div className="productInf">
        <div className="box-image">
          <img src={product[0].image2} alt="product" width={400} height={550} />
        </div>
        <div className="info">
          <h5>{product[0].title}</h5>
          <p>M s: #{product[0].id}</p>
          {product[0].price ? (
            <p className="price">{product[0].price} </p>
          ) : (
            <div>
              <p className="priceOrg">
                Gi gc: <span>{product[0].priceOrg} </span> 
              </p>
              <p className="priceSale">
                Gi Sale: <span>{product[0].priceSale}</span> 
              </p>
            </div>
          )}

          <div className="actGroup">
            <Box sx={{ minWidth: 120 }} className="chooseSize">
              <FormControl size="small">
                <InputLabel id="demo-simple-select-label">Size</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value={size}
                  label="Size"
                  onChange={handleChange}
                >
                  <MenuItem value={"S"}>S</MenuItem>
                  <MenuItem value={"M"}>M</MenuItem>
                  <MenuItem value={"L"}>L</MenuItem>
                  <MenuItem value={"XL"}>XL</MenuItem>
                </Select>
              </FormControl>
            </Box>
            <Button
              variant="contained"
              color="success"
              onClick={() => add(product[0], size)}
            >
              ADD TO CART
            </Button>
          </div>
          <div className="describe">
            <p>M t sn phm</p>
            <span> Cht liu: French Terry</span>
            <span>Thnh phn :100% cotton</span>
            <ul>
              <li>Thn thin</li>
              <li>Thm ht thot m</li>
              <li>Mm mi</li>
              <li>Kim sot mi</li>
              <li>iu ha nhit</li>
            </ul>
          </div>
        </div>
      </div>
      <div className="sampleList">
        <img src={IMG_Mota} alt="mau" />
        <img src={IMG_Mota} alt="mau" />
        <img src={IMG_Mota} alt="mau" />
        <img src={IMG_Mota} alt="mau" />
      </div>
    </div>
  );
};

export default Details;

