import { React, useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "./carts.scss";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import HighlightOffIcon from "@mui/icons-material/HighlightOff";
import { useSelector, useDispatch } from "react-redux";
import { RMV, ADD, DLT } from "../../redux/actions/action";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import validator from "validator";

import Empty from "../../assets/IMG_Empty.png";

const Carts = () => {
  const getCart = useSelector((state) => state.cartreducer.carts);
  const dispatch = useDispatch();

  const [total, setTotal] = useState(0);
  const [data, setData] = useState([]);

  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAD] = useState("");

  const add = (e) => {
    dispatch(ADD(e));
  };
  const dltOne = (id) => {
    dispatch(DLT(id));
  };
  const dlt = (id) => {
    dispatch(RMV(id));
  };
  const VND = new Intl.NumberFormat("vi-VN", {
    style: "currency",
    currency: "VND",
  });

  const notifySuccess = (message) =>
    toast.success(message, {
      position: "top-right",
      autoClose: 2000,
      hideProgressBar: false,
      closeOnClick: true,
      draggable: true,
      progress: undefined,
      theme: "light",
    });

  const notifyWarn = (message) =>
    toast.warn(message, {
      position: "top-right",
      autoClose: 2000,
      hideProgressBar: false,
      closeOnClick: true,
      draggable: true,
      progress: undefined,
      theme: "light",
    });

  const validatePhoneNumber = (number) => {
    const isValidPhoneNumber = validator.isMobilePhone(number);
    return isValidPhoneNumber;
  };

  const order = (e) => {
    if (name === "" || phone === "" || address === "") {
      e.preventDefault();
      notifyWarn("in y  thng tin");
    } else if (!validatePhoneNumber(phone)) {
      notifyWarn("S in thoi khng ng");
      e.preventDefault();
    } else {
      notifySuccess("t hng thnh cng");
      setName("");
      setPhone("");
      setAD("");
    }
  };
  useEffect(() => {
    setData(getCart);
    const sum = () => {
      let price = 0;
      getCart.map((ele) => {
        return ele?.price
          ? (price = price + parseInt(ele.price) * ele.qty)
          : (price = price + parseInt(ele.priceSale) * ele.qty);
      });
      price = price + "000";
      setTotal(VND.format(price));
    };

    sum();
  }, [getCart]);

  return (
    <div className="cartContainer">
      <ToastContainer />

      {data.length === 0 ? (
        <div className="emptyCart">
          <h3>Bn cha chn sn phm.</h3>
          <img src={Empty} alt="empty" />
          <p>Hy nhanh tay chn ngay sn phm yu thch.</p>
        </div>
      ) : (
        <div className="wrapper">
          <div className="cartList">
            <h3>CHI TIT N HNG</h3>
            <table>
              <tr>
                <th></th>
                <th>SN PHM</th>
                <th>GI</th>
                <th>S LNG</th>
                <th>TNG</th>
              </tr>
              {getCart?.map((item) => {
                return (
                  <tr>
                    <td className="btnDel">
                      <HighlightOffIcon onClick={() => dlt(item.id)} />
                    </td>
                    <td className="product">
                      <Link to={`/details/${item.id}`}>
                        <img src={item.image1} alt="product" width={100} />
                      </Link>
                      <p className="itemTitle">
                        <p className="titleProduct">{item.title}</p>
                        <p>Size: {item.size}</p>
                      </p>
                    </td>
                    <td className="donGia">{item.price || item.priceSale} </td>
                    <td className="soLg">
                      <span>
                        <button onClick={() => dltOne(item.id)}>-</button>
                      </span>
                      <span>{item.qty}</span>
                      <span>
                        <button onClick={() => add(item)}>+</button>
                      </span>
                    </td>
                    <td className="total">
                      {VND.format(
                        item.priceSale
                          ? parseInt(item.priceSale) * item.qty + "000"
                          : parseInt(item.price) * item.qty + "000"
                      )}
                    </td>
                  </tr>
                );
              })}
            </table>
            <div className="checkout">
              <span>Thnh tin:</span> {total}
            </div>
          </div>
          <div className="address">
            <h3>NGI MUA/NHN HNG</h3>
            <Form onSubmit={order}>
              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Tn*</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Tn ngi nhn"
                  onChange={(e) => {
                    setName(e.target.value);
                  }}
                  value={name}
                />
              </Form.Group>

              <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Label>in thoi lin lc*</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="S in thoi"
                  onChange={(e) => {
                    setPhone(e.target.value);
                  }}
                  value={phone}
                />
              </Form.Group>

              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Nhn hng ti nh/cng ty/bu in*</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="a ch nhn hng"
                  onChange={(e) => {
                    setAD(e.target.value);
                  }}
                  value={address}
                />
              </Form.Group>

              <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Label>Ghi ch</Form.Label>
                <Form.Control as="textarea" />
              </Form.Group>
              <Button variant="primary" type="submit">
                T HNG
              </Button>
            </Form>
          </div>
        </div>
      )}
    </div>
  );
};

export default Carts;
